*{
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
  font-family: 'Josefin Sans', sans-serif;
}
.main_div{
  width: 100%;
  height: 100vh;
  background: #7bedf7;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width: 25%;
  height: 70vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px(0,0,0,0.5);
  border-radius: 15px;
}
h1{
  color: rgba(0,0,0,0.87);
  font-size: 50px;
  margin-bottom: 30px;
  min-width: 64px;
  box-sizing: border-box;
  font-family: 'Kdam Thmor Pro', sans-serif;
  font-weight: 500;
  border-radius: 4px;
  letter-spacing: 0.02857;
  text-transform: uppercase;
  box-shadow: 5px 5px 25px -5px(0,0,0,0.5);
}
input{
  text-align: center;
  height:30px;
  border: none;
  background: transparent;
  font-size: 30px;
  font-weight: 700;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-right: 20px;
}
.newBtn{
  width:50px;
  height: 55px;
  background-color: #16a085 !important;
  box-shadow: 5px 5px 25px -5px(0,0,0,0.5);
  border-radius: 50% !important;
}
.newBtn:hover{
  color: #16a085 !important;
  background: white !important;
}
.MuiSvglon-root{
  width: 2rem !important;
  height: 2rem !important;
}
ol{
  margin-top: 30px;
}

ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 30px;
  font-weight: 500;
  font-family: 'Kdam Thmor Pro', sans-serif;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  list-style: none;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

}
.todo_style .deleteIcon{
  width: 30px;
  height: 30px;
  display:flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  padding: 5px;
  color:aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3); 
}
.todo_style .deleteIcon:hover{
  background-color: transparent;
  color:crimson;
  box-shadow: 5px 5px 15px -5px rgba(225,0,0,0.5); 
}
.todo_style .fa-times:hover .list_style{
  color:crimson;
}

@media(max-width : 786px){
  .center_div{
    width: 55%;
  }
  .todo_style .fa-times{
    margin:0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color:#fff;
    font-size: 30px;
  }
}
